import React, { Component } from 'react';
import { Slider, Slide } from 'react-materialize';

class MainSlider extends Component {
    render() {
        return ( 
            <div className="mainSlider">
                <Slider indicators={false} interval={4000} >
                    <Slide
                        src="/imgs/img-1.jpg"
                        title="Welcome to Nancy's Jewelry">
                        We are the most affordable Jewerly Store in Belize
                    </Slide>
                    <Slide
                        src="/imgs/img-4.jpg"
                        title="Let us help your with you Jewelry Issues"
                        placement="left">
                        We promise High Quality Work
                    </Slide>
                    <Slide
                        src="/imgs/img-5.jpg"
                        title="We have a range of services"
                        placement="right">
                        Our customers are our priority!
                    </Slide>
                </Slider>
            </div>  
        );
    }
}

export default MainSlider;


