import React, { Component } from 'react';

class Footer extends Component {
    render() {
        return (
            <div>
                <footer className="page-footer"> 
                    <div className="footer-copyright">
                        <div className="container">
                            Copyright © 2018 <a className="grey-text text-lighten-4" href="http://themeforest.net/user/geekslabs/portfolio?ref=geekslabs" target="_blank">Nancy's Jewelry</a> All rights reserved.
                            <span className="right"> Design and Developed by <a className="grey-text text-lighten-4" href="https://lmanzanero.github.io/portfolio/">Luis Manzanero</a></span>
                        </div>
                    </div>
                </footer>
            </div>
        );
    }
}

export default Footer;


