import React, { Component } from 'react';

//components
import Home from '../Sections/Home'; 

class MainLayout extends Component {
    render() {
        return (
            <div>
                <Home/>
                <div className="row">
                    <div className="col s12 m9 l10">
                    <div id="latest-deals" className="section scrollspy">
                        <p>Latest Deals</p>
                    </div>

                    <div id="services" className="section scrollspy">
                        <p>Services </p>
                    </div>

                    <div id="contact" className="section scrollspy">
                        <p>Contact</p>
                    </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default MainLayout;


